
ul {
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: wheat;
  }
.background{
    position:fixed;
    top:0%;
    width:100%;
  }
.navtitle{
    font-stretch: wider;
    border-style: none;
    border-radius: 20%;
    font-family: 'Bebas Neue', cursive ;
    font-size:35px;
    float: left;
    display: block;
    color: red;
    text-align: center;
    padding: 5px;
    text-decoration: none;
}
.navitem{
    font-size:20px;
    float: right;
    display: block;
    color: #111111;
    text-align: center;
    padding : 8px;
    text-decoration: underline;
  }
  